<template>
 <v-card id="portfolio" flat color="transparent" max-width="350px">
    <v-card-title>
      <v-layout>
        <v-flex >
          <v-card
           
            target="_blank"
            elevation="5"
            style="border-radius: 20px"
            width="100px"
            height="100px"
          >


<!-- 
DIALOOGGGG
 -->



    <v-dialog
      v-model="dialog"
      width="50%"
     
      
    >


<!-- 
  DIALOGGGGG END
 -->


       <template v-slot:activator="{ on }">     <v-img   v-on="on" min-width="100%" min-height="100%"    :src="logo(app.logo)" />   </template> 
   




<v-carousel >
    <v-carousel-item
      v-for="(slide, i) in app.sliders"
      :key="i"
      
      :src="carouselItems(slide.src)"
    >
     
    </v-carousel-item>
  </v-carousel>

    </v-dialog>
 


<!-- END DIALOG 2 -->


            
          </v-card>
        </v-flex>
        <v-flex xs7>
          <div>{{app.name}}</div>
          <div>
            <v-chip small pill color="blue" dark>{{app.price}}</v-chip>
                  <div v-if="app.code.link">
     
        <v-btn icon :href="app.code.link" target="_blank">
          <v-icon>{{app.code.icon}}</v-icon>
        </v-btn>
      </div>
          </div>

          <template v-for="(platform, i) in app.platforms">
            <!-- <div> -->
            <v-btn :href="platform.link" target="_blank" icon :key="i">
              <v-icon>{{platform.icon}}</v-icon>
            </v-btn>
            <!-- </div> -->
          </template>
        </v-flex>
      </v-layout>
    </v-card-title>
    <v-card-text>
      <div>{{app.description}}</div>
      <div class="title mt-2">Technologies Utilises</div>
      <v-layout>
        <template  v-for="(tech, i) in app.technologies">
          <v-flex xs3 :key="i">
            <div class="  text-center">
              <v-btn icon>
                <v-icon>{{tech.icon}}</v-icon>
              </v-btn>
              <div class="caption">{{tech.name}}</div>
            </div>
          </v-flex>
        </template>
      </v-layout>

    </v-card-text>
  </v-card>
</template>


<script>

export default {
  props: {
    app: {
      type: Object,
      default: function() {
        return {
          
        };
      }
    }
  },
    data: function () {
    return {
      dialog: false,
      
    }
  },
  methods: {
    logo(name) {
      return require("../assets/" + name);
    },
    carouselItems(name) {
      return require("../assets/" + name);
    },
  }
};
</script>

<style scoped>
#portfolio{
 
margin-right: auto;
margin-left: auto; 
  text-align: center;
}
</style>
